#projects #home_first {
    margin-bottom: 3.5rem;
}


#home_first>div:nth-child(2),
#home_second>div:nth-child(2) {
    padding: 30px 50px 0 50px;
}

#home_second>div:nth-child(2) {
    padding: 0px 50px 0 50px;
}


#home_first>div:nth-child(1) {
    order: 2;
}

#home_second>div:nth-child(2) {
    order: 1;
}

.project_desc>div {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
}

.project_desc>div>div {
    border: 1px solid grey;
    border-radius: 10px;
    padding: 5px 10px;
}

.stack_btn>div:nth-of-type(1) {
    font-size: 0.8rem;
    flex-wrap: wrap !important;
}