.main_desc {
    margin-top: 13rem;
    border-bottom: 1px solid rgba(128, 128, 128, 0.582);
    padding-bottom: 1.5rem;
    position: relative;
}

.main_desc>h2 {
    margin-bottom: 0;
}

.main_desc>h2:nth-of-type(1) {
    font-size: 4rem;
    letter-spacing: 0.2rem;
}

.main_desc>h2:nth-of-type(2) {
    font-size: 1.2rem;
    text-indent: 0.5rem;
    letter-spacing: 0.1rem;
}

.main_desc>span {
    font-size: 1.3rem;
    animation: sparkle 2s infinite;
}

.main_privacy {
    margin-top: 21%;
    display: flex;
    flex-direction: column;
    gap: 0.7rem;
    position: absolute;
    right: 0;
}

.main_privacy>div {
    display: flex;
    gap: 2rem;
}

.home {
    position: relative;
}

@keyframes sparkle {

    0%,
    100% {
        filter: drop-shadow(0 0 15px gold);
    }

    50% {
        filter: drop-shadow(0 0 0px gold);
    }
}