#fishing section {
    margin-bottom: 25%;
}


#fishing section>div:nth-child(1) {
    display: flex;
    align-items: center;
}

#fishing h1 {
    text-align: center;
    font-size: 2rem;
    padding-bottom: 1%;
    border-bottom: 1px solid rgba(128, 128, 128, 0.577);
    margin-bottom: 7%
}

#fishing h2 {
    margin-bottom: 0;
}

#fishing section p {
    /* font-size: small; */
    font-size: 0.9rem;
    padding-left: 20px;
}

.videoplayer {
    height: 20rem !important;
}

.skill_title {
    font-size: x-large;
}

.fishingLogo {
    display: flex;
    flex-wrap: wrap;
    gap: 5%;
}

.fishingLogo>div {
    width: 20%;
    overflow: hidden;
    border-radius: 15px;
}

.fishingLogo>div:nth-child(n+1):nth-child(-n+8) {
    margin-bottom: 4%;
}

.fishingLogo>div>img {
    width: 100%;
    height: 100%;
}



.boiling {
    animation: jump 1s infinite;
    display: block !important;
    top: -0.5rem !important;
    right: -1.5rem !important;

}


@keyframes jump {


    0% {
        transform: translateX(30px);
    }
}