#tomato section {
    margin-bottom: 25%;
}


#tomato section>div:nth-child(1) {
    display: flex;
    align-items: center;
}

#tomato h1 {
    text-align: center;
    font-size: 2rem;
    padding-bottom: 1%;
    border-bottom: 1px solid rgba(128, 128, 128, 0.577);
    margin-bottom: 4%;
}

#tomato h2 {
    margin-bottom: 0;
}

#tomato section p {
    /* font-size: small; */
    font-size: 0.9rem;
    padding-left: 20px;
}

.videoplayer {
    height: 20rem !important;
}



#tomato .linkBox>div:hover {
    background-color: #9b1b20;
    color: white;
    box-shadow: 0px 0px 5px 3px #9b1b20;
}

.waveTomato {
    animation: wave 1s infinite;
    display: block !important;
}

@keyframes wave {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }

    25% {
        transform: translate(-50%, -50%) rotate(-10deg);
    }

    75% {
        transform: translate(-50%, -50%) rotate(10deg);
    }

    100% {
        transform: translate(-50%, -50%) rotate(0deg);
    }
}