@media screen and (max-width: 853px) {

    * {
        font-size: 1rem;
    }


    /*  =============== Header =============  */

    #menu_box li a {
        font-size: 1.0rem !important;
    }

    #menu_box {
        padding-top: 14% !important;
    }

    .stack_btn>div:nth-of-type(1) {
        font-size: 0.6rem !important;
        flex-wrap: wrap !important;
    }

    /*  =============== Main =============  */

    #main_container {
        margin-top: 20%;
    }


    section {
        flex-direction: column;
        margin-top: 10%;
    }

    section>div {
        width: 100%;
    }

    #home_first>div:nth-child(2) {
        order: 1;
    }

    .project_desc>h2 {
        margin-top: 10px;
        text-align: center;
    }

    #home_first>div:nth-child(1) {
        order: 1 !important;
    }

    #home_first>div:nth-child(2),
    #home_second>div:nth-child(2) {
        padding: 20px 10px 0 10px !important;
    }

    .main_desc {
        margin-top: 0 !important;
    }

    .main_privacy {
        padding-bottom: 6rem !important;
    }

    .main_desc img {
        display: none;
    }

    h1 {
        font-size: 2.2rem;
    }

    #career_section>div:nth-of-type(1) {
        margin-bottom: 10%;
    }

    .logoBox {
        flex-wrap: wrap;
    }

    .logoBox>div {
        font-size: 0.7rem !important;
        width: 15% !important;
    }


    .videoplayer {
        height: 10rem !important;
    }

    .upBtn {
        line-height: 3rem;
        bottom: 1rem;
        right: 1rem;
        width: 2.5rem;
        height: 2.5rem;
    }

    .upBtn>a {
        width: 100%;
        height: 100%;
        font-size: 0.5rem;
    }

    .container {
        width: 85% !important;
    }


}