.App {
  font-family: "Noto Sans KR", sans-serif;
  height: 100%;
}

html {
  background-color: black;
  color: white;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  word-break: keep-all;
}

a {
  text-decoration: none;
  color: white;
}

li {
  list-style-type: none;
}

hr {
  margin-bottom: 18%;
}



section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10%;
}

section>div {
  width: 47%;
  overflow: hidden;
}

section img {
  width: 100%;
  /* height: 100%; */
  object-fit: fill;
}

.detail {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(43deg, #a162e8, #f7ce68 46%, #85ffbd);
  margin-bottom: 4%;
  margin-left: 10px;
}

.detail:hover {
  font-weight: 900;
  font-stretch: expanded;
}

h2 {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(43deg, #a162e8, #f7ce68 46%, #85ffbd);
  margin-bottom: 4%;
  font-size: 1.9rem;
}

h2>a {
  font-weight: 300;
  font-size: smaller;
}

.container {
  width: 55%;
  margin: auto;
  height: 100%;
}

#main_container {
  margin-top: 10%;
}

.title {
  border-bottom: 1px solid rgba(128, 128, 128, 0.582);
  margin-bottom: 3rem;
  padding-bottom: 0.5rem;
  text-align: center;
}

.upBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  font-size: 1.7rem;
  bottom: 2.7rem;
  right: 2.7rem;
  width: 2.7rem;
  height: 2.7rem;
  background-color: rgb(128, 128, 128);
  border-radius: 50px;
  cursor: pointer;
  text-align: center;
}

.upBtn>a {
  color: black;
}

.linkBox {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 3rem;
}

.linkBox>div {
  width: 45%;
  text-align: center;
  background-color: grey;
  color: black;
  padding: 10px 0px;
  font-weight: 700;
  border-radius: 20px;
  position: relative;
}

.linkBox>div>a {
  width: 100%;
  height: 100%;
  cursor: pointer;
  display: inline-block;
  color: black;
}

.linkBox>div:hover {
  background-color: rgba(0, 255, 255, 0.881);
  transform: scale(1.1);
  box-shadow: 0px 0px 5px 3px rgba(0, 255, 255, 0.881);
}

.linkBox .link_Hi {
  display: block;
  font-size: 2rem;
  position: absolute;
  top: -0.7rem;
  right: -2.4rem;
  display: none;
}



/* ============================ */