.aboutme h2 {
    margin-bottom: 0;
}


.aboutme .detail {
    font-size: small;
}

.aboutme>div {
    margin-bottom: 10rem;
}

.logoBox {
    margin: 1rem 0rem 2rem 0rem;
    gap: 3rem;
    flex-wrap: wrap;
    display: flex;
    font-size: 0.6rem;
}

.logoBox div {
    width: 7%;
    border-radius: 10px;
    text-align: center;
}

.logoBox img {
    width: 100%;
    height: 70%;
    border-radius: 10px;
}

#imgBox {
    width: 100%;
    overflow: hidden;
}

#imgBox img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#imgBox>div {
    width: 100%;
}

#career_section {
    display: flex;
    align-items: center;
    margin-bottom: 15%;
}



#career_section p {
    font-size: smaller;
    padding-left: 20px;
}

#career_section2 p {
    font-size: smaller;
    padding-left: 20px;
}

#career_section img {
    width: 50%;
}

.career_title {
    font-size: large;
}

#career_section2>div:nth-of-type(1) {
    display: flex;
    align-items: center;
}

#career_section2 img {
    height: 70%;
}